.aboutme {
    min-height: 100vh;
}
.aboutme  h1 {
        font-size: 50px;
        /*text-align: center;*/
        text-decoration: underline;
}
.aboutme  .my_image {
    max-width: 250px;
    max-height: 250px;
    margin: 0 auto;
    display: flex;
    border-radius: 100%;
    transform: scale(1);
    transition: all 0.3s;
}
.aboutme .my_image_wrapper {
    max-width: 250px;
    max-height: 250px;
    margin: 0 auto;
    border-radius: 100%;
    display: flex;
}
.image {
    display: none;
}
.image2 {
    display: unset;
}
.aboutme .my_image_wrapper1 {
    max-width: 250px;
    max-height: 250px;
    margin: 0 auto;
    border-radius: 100%;
    display: flex;
}

@media screen and (max-width: 768px) {
    .image {
        display: unset;
    }
    .image2 {
        display: none;
    }

    .aboutme p {
        font-size: 20px !important;
        text-align: justify;
        letter-spacing: 1px;
        word-spacing: 2px;
    }
    .aboutMeHeader {
        text-align: center !important;
    }
    .aboutme {
        padding-top: 0 !important;
    }
    .aboutmeRow {
        padding-top: 0 !important;
    }
}

.aboutme .my_image_wrapper:hover {
    cursor: pointer;
}
.my_image_wrapper:hover .my_image {
    transform: scale(1.1);
}

.aboutme p {
    font-size: 25px;
    text-align: justify;
    letter-spacing: 2px;
    word-spacing: 3px;
}
