
.overlay {
    height: 0;
    width: 0;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    transition: 0.5s;
}

.overlay-content {
    position: relative;
    top: 10%;
    height: 0;
    text-align: center;
    margin-top: 30px;
}
.overlay-content .my_image {
    width: 250px;
    height: auto;
    border-radius: 50%;
}
.overlay-content h4 {
    font-size: 30px;
    padding-top: 15px;
    color: #fff;
}
.overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
    color: #f1f1f1;
}

.overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
    border: none;
    background: transparent;
    color: #fff;
}
.overlay .closebtn:focus {
    outline: none;
}
.menuIcon {
    background: transparent;
    color: #fff;
    border: none;
    margin-right: 10px;
    font-size: 25px;
}
.menuIcon:focus {
    outline: none;
}
@media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
    .overlay-content .my_image {
        width: 150px
    }
    .overlay-content h4 {
        font-size: 25px;
    }
}
