.jumbotron_wrapper {
    background-image: linear-gradient(rgba(255, 252, 252, 0.5), rgba(255, 255, 255, 0.5)), url("../../assets/images/photo-1461749280684-dccba630e2f6.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    margin-bottom: unset;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.jumbotron_wrapper .wrap {
    color: black;
    font-size: 75px;
}
.jumbotron_wrapper .down_arrow {
    position: absolute;
    top: 90%;
    left: 0;
    right: 0;
    font-size: 30px;
    color: black;
    animation-name: down_arrow_animation;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}
@keyframes down_arrow_animation {
    0% {
        top: 90%;
        color: black;
    }
    50% {
        top: 91%;
        color: lightgray;
    }
    100% {
        top: 90%;
        color: black;
    }
}
.animate {
    font-size: 100px;
}
.typewrite:hover {
    text-decoration: unset;
    cursor: unset;
}

@media screen and (max-width: 1000px) {
    .animate {
        font-size: 75px;
    }
    .jumbotron_wrapper .wrap {
        color: black;
        font-size: 50px;
    }
}
@media screen and (max-width: 800px) {
    .animate {
        font-size: 50px;
    }
    .jumbotron_wrapper .wrap {
        color: black;
        font-size: 35px;
    }
}
@media screen and (max-width: 500px) {
    .animate {
        font-size: 35px;
    }
    .jumbotron_wrapper .wrap {
        color: black;
        font-size: 25px;
    }
}
@media screen and (max-width: 370px) {
    .animate {
        font-size: 25px;
    }
    .jumbotron_wrapper .wrap {
        color: black;
        font-size: 15px;
    }
}

.animate span {
    display: inline-block;
}
.animate span:nth-of-type(2) {
    animation-delay: .05s;
}
.animate span:nth-of-type(3) {
    animation-delay: .1s;
}
.animate span:nth-of-type(4) {
    animation-delay: .15s;
}
.animate span:nth-of-type(5) {
    animation-delay: .2s;
}
.animate span:nth-of-type(6) {
    animation-delay: .25s;
}
.animate span:nth-of-type(7) {
    animation-delay: .3s;
}
.animate span:nth-of-type(8) {
    animation-delay: .35s;
}
.animate span:nth-of-type(9) {
    animation-delay: .4s;
}
.animate span:nth-of-type(10) {
    animation-delay: .45s;
}

/* Animation One */

.one span {
    color: #24a8e6;
    opacity: 0;
    transform: translate(-150px, -50px) rotate(-180deg) scale(3);
    animation: revolveScale .4s forwards;
}

@keyframes revolveScale {
    60% {
        transform: translate(20px, 20px) rotate(30deg) scale(.3);
    }

    100% {
        transform: translate(0) rotate(0) scale(1);
        opacity: 1;
    }
}
@keyframes dropVanish {
    30% {
        transform: translate(0, -50px) rotate(180deg) scale(1);
    }

    50% {
        transform: translate(0, 20px) scale(.8) rotate(0deg);
        opacity: 1;
    }

    80% {
        transform: translate(-100px, -100px) scale(1.5) rotate(-180deg);
        opacity: 0;
    }

    100% {
        transform: translate(0) scale(1) rotate(0deg);
        opacity: 1;
    }
}


@keyframes twister {
    10% {
        opacity: 1;
    }
    100% {
        transform: rotate(0deg) translate(0);
        opacity: 1;
    }
}

.seven span {
    color: #348c04;
    opacity: 0;
    transform: translate(-150px, 0) scale(.3);
    animation: leftRight .5s forwards;
}


@keyframes leftRight {
    40% {
        transform: translate(50px, 0) scale(.7);
        opacity: 1;
        color: #348c04;
    }

    60% {
        color: #0f40ba;
    }

    80% {
        transform: translate(0) scale(2);
        opacity: 0;
    }

    100% {
        transform: translate(0) scale(1);
        opacity: 1;
    }
}

